
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-bottom: 20px;
}
html {
  height: 100%;
}
body {
  background: linear-gradient(135deg, #68c9b6, #3677ce) no-repeat;
  background-attachment: fixed;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: #0060E9;
}
a:hover {
    text-decoration: underline;
    color: #FF6C09;
}
ul, li {
  list-style: none;
}
::-webkit-scrollbar {
  width: 0.5em;
}
::-webkit-scrollbar-track {
  border-radius: 0.25em;
  background: #ceb9d1;
}
::-webkit-scrollbar-thumb {
  height: 0.5em;
  border-radius: 0.25em;
  background: #498bd6 -webkit-gradient(linear, left top, left bottom, from(#fff), color-stop(rgba(3, 169, 244, 0.5)), to(#0277bd));
  background: #498bd6 linear-gradient(180deg, #fff, rgba(3, 169, 244, 0.5), #0277bd);
}
button {
  display: inline-block;
  cursor: pointer;
  border: none;
  outline: none;
  -webkit-appearance: none;
  color: #fff;
  overflow: hidden;
  background: #7e57c2;
  border-radius: 2px;
  height: 36px;
  line-height: 36px;
  padding: 0;
  min-width: 88px;
  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.4), 0 1px 4px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.4), 0 1px 4px rgba(0, 0, 0, 0.2);
}
pre {
  border: 2px solid #8BE388;
  -moz-tab-size: 4;
    -o-tab-size: 4;
       tab-size: 4;
  word-wrap: break-word;
  white-space: pre-wrap;
}
pre + pre {
  margin-top: 0.5em;
}
code {
  font-family: Consolas,Monaco;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: left;
}



