
.rippleWrapper.ib {
  display: inline-block;
}
.ripple {
    position: relative;
}
.slot {
   position: relative;
}
.slot.one .children>* {
     position: relative;
     z-index: 1;
}
.slot.two .children *>* {
     position: relative;
     z-index: 1;
}
.cov-button-ripple {
     position: absolute;   
     top: 0;
     left: 0;
     right: 0;
     bottom: 0;
     overflow: hidden;
     cursor: pointer;
}
.cov-purebutton-ripple {
     position: relative;
     overflow: hidden;
     cursor: pointer;
}
.cov-button-ripple.br,
 .cov-purebutton-ripple.br {
     border-radius: 50%;
}
.cov-ripple {
     display: block; 
     position: absolute;
     background: #D8C9F8;
     border-radius: 100%;
     -webkit-transform: scale(0);
             transform: scale(0);
     z-index: 2;
}
.cov-ripple.animate {
     -webkit-animation: ripple 0.65s linear;
             animation: ripple 0.65s linear;
}
@-webkit-keyframes ripple {
100% {
         opacity: 0;
         -webkit-transform: scale(3);
                 transform: scale(3);
}
}
@keyframes ripple {
100% {
         opacity: 0;
         -webkit-transform: scale(3);
                 transform: scale(3);
}
}



